import * as React from 'react';
import { useNavigate } from 'react-router-dom';

import lang from './views.lang';

import Staff from '@miq/staffjs';
import { useDocumentTitle } from '@miq/hookjs';

import type { TProduct } from '../types';

import { ProductCreateForm } from './forms';

const { i18n } = window;

export default function ProductCreateView() {
  useDocumentTitle('Add a product - Store');
  const nav = useNavigate();
  return (
    <Staff.View title={i18n(lang, 'CreateView.title')} requiredPerms={['store.add_product']} back="../">
      <Staff.Section>
        <ProductCreateForm onSuccess={({ data }: { data: TProduct }) => nav(`../${data.slug}`)} />
      </Staff.Section>
    </Staff.View>
  );
}
